html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  min-height: 100vh;
}

body,
h1,
h2,
h3,
h4,
p,
blockquote,
figure {
  margin: 0;
}

a {
  outline: none;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

input,
button,
textarea {
  border: none;
  font: inherit;
  outline: none;
}

button {
  color: inherit;
  cursor: pointer;
}

body {
  font-family: "Barlow Semi Condensed", sans-serif;
  font-weight: 500;
  background: #ecf2f8;
}

.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

.grid-container {
  max-width: 69.4375rem;
  width: 90%;
  margin: 0 auto;
  min-height: 100vh;
  display: grid;
  justify-content: center;
  align-content: center;
  row-gap: 1.5rem;
  column-gap: 1.875rem;
  grid-template-areas: "daniel" "jonathan" "jeanette" "patrick" "kira";
}
@media (min-width: 50em) {
  .grid-container {
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: "daniel daniel jonathan" "jeanette patrick patrick" "kira kira kira";
  }
}
@media (min-width: 65em) {
  .grid-container {
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: "daniel daniel jonathan kira" "jeanette patrick patrick kira";
  }
}

.card {
  padding: 1.875rem;
  line-height: 1.3;
  color: white;
  border-radius: 0.625rem;
  box-shadow: 2.1875rem 3.375rem 2.8125rem -2.5rem rgba(72, 85, 106, 0.3);
  max-width: 28.75rem;
}
@media (min-width: 50em) {
  .card {
    max-width: 100%;
  }
}
.card--violet {
  grid-area: daniel;
  background-color: #7541c8;
  background-image: url(../images/bg-pattern-quotation.svg);
  background-repeat: no-repeat;
  background-position-x: 80%;
}
.card--grey {
  grid-area: jonathan;
  background-color: #48556a;
}
.card--white {
  background-color: white;
  color: #48556a;
}
.card--blue {
  background-color: #19212e;
  grid-area: patrick;
}
.card--kira {
  grid-area: kira;
}
.card--jeanette {
  grid-area: jeanette;
}
.card__title {
  margin-top: 0.6875rem;
  margin-bottom: 1.0625rem;
  font-size: 1.25rem;
  font-weight: 600;
  letter-spacing: 0.045px;
}
.card__testimonial {
  opacity: 0.7;
  font-size: 0.84375rem;
}

.profile {
  display: flex;
  align-items: center;
}
.profile__img {
  height: 2rem;
  border-radius: 50%;
  margin-right: 0.9375rem;
}
.profile__img--violet {
  border: 2px solid #9a7bcc;
}
.profile__title {
  font-size: 0.6875rem;
  font-weight: 500;
}
.profile__graduate {
  opacity: 0.5;
  font-size: 0.625rem;
}

.attribution {
  font-size: 0.75rem;
  text-align: center;
  padding: 0.75rem 0;
}

.attribution a {
  color: #3e52a3;
  display: inline-block;
  transition: transform 0.3s ease-out;
}
.attribution a:hover {
  transform: translateY(-5px);
  color: #7541c8;
  font-size: 0.8125rem;
}